.header-socials__link {
  font-family: "Roboto Condensed";
  font-style: normal;
  font-size: 12px;
  line-height: 14px;
  color: #ffffff;
  text-decoration: none;
  text-transform: uppercase;
  transition: 0.5s;

  &:hover {
    text-shadow: 3px 3px 5px black;
    transition: 0.5s;
  }
}
